<template>
  <div v-if="album" class="base-title-cover-container">
    <div class="cover-region">
      <img
        class="back-cover"
        :src="album.cover"
      />
      <p
        :class="['update', false ? 'hot' : '']"
      >
        {{ album.contentType | fltTemplateName }}
      </p>
      <p v-if="album.lastPlayTime" class="recent">
        {{ album.lastPlayTime | fltTimeInterval }}听过
      </p>
    </div>
    <p class="album-title two-ellipsis">{{ album.name }}</p>
    <div v-if="!album.isLock" class="price f-i-end-wrap">
      <p class="new-price">
        ¥{{ album.spuInfo.price | fltToDecimal2 }}
      </p>
      <p v-if="album.spuInfo.orgiPrice !== album.spuInfo.price" class="old-price">
        ¥{{ album.spuInfo.orgiPrice | fltToDecimal2 }}
      </p>
    </div>
    <!-- <p class="had-hear">
      已听60%
    </p> -->
  </div>
</template>

<script>
export default {
  props: {
    album: {
      type: Object,
      default: () => null
    }
  },

  data() {
    return {

    }
  }
}
</script>

<style lang="scss" scoped>
.base-title-cover-container {
  width: 100%;
  position: relative;
  
  .cover-region {
    width: 100%;
    min-height: 140px;
    position: relative;

    .back-cover {
      box-shadow: 0 0 1px #dfdfdf inset;
      width: 100%;
      display: block;
      min-height: 140px;
      border-radius: 5px;
      object-fit: cover;
    }
    .update {
      position: absolute;
      right: 0;
      top: 0;
      background: rgba(0,0,0,0.4);
      border-bottom-left-radius: 5px;
      border-top-right-radius: 5px;
      font-size: 10px;
      color: rgba(255,255,255,1);
      padding: 0 5px;
    }
    .hot {
      background: rgba(214,66,66,1);
    }
    .recent {
      position: absolute;
      left: 0;
      bottom: 0;
      background: rgba(0,0,0,0.4);
      border-bottom-left-radius: 5px;
      border-top-right-radius: 5px;
      font-size: 10px;
      color: rgba(255,255,255,1);
      padding: 0 5px;
    }
  }
  .album-title {
    width: 100%;
    box-sizing: border-box;
    margin-top: 10px;
    font-size: 14px;
    color: rgba(34,34,34,1);
    line-height: 1.2;
    padding: 0 4.5px;
  }
  .price {
    margin-top: 8px;
    width: 100%;
    padding: 0 4.5px;

    .new-price {
      color: rgba(239,60,60,1);
      font-weight: 600;
      font-size: 15px;
      margin-right: 5px;
    }
    .old-price {
      text-decoration: line-through;
      color: #BBBBBB;
      font-size: 11px;
    }
  }
  .had-hear {
    margin-top: 8px;
    font-size: 12px;
    color: rgba(187,187,187,1);
    padding: 0 4.5px;
    width: 100%;
    box-sizing: border-box;
  }
}
</style>


